.item-page {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;

  .edit-area {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    .edit-select {
      width: 300px;
      margin-bottom: 10px;
    }
    .edit-input {
      width: 300px;
      height: 36px;
      padding: 0px 10px;
      box-sizing: border-box;
      margin-bottom: 10px;
      text-align: right;
    }
    .edit-btn {
      width: 300px;
    }
  }

  .btn-area {
    position: absolute;
    right: 20px;
    bottom: 20px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    box-sizing: border-box;

    input {
      width: 245px;
      height: 50px;
      border: 2px solid #2c3333;
      border-radius: 25px;
      box-sizing: border-box;
      padding: 0px 20px;
      outline: none;
      font-size: 16px;
      transition: width 0.5s ease-in-out;

      // &:focus {
      //   width: 400px;
      // }
    }
  }
}

.item__log--container {
  width: 100%;
  height: 100%;
  overflow-y: auto;

  &::-webkit-scrollbar {
    width: 3px;
    height: 3px;
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: #2c3333;
  }

  .item__log--ul {
    .item__log--seperator {
      padding: 10px 5px;
      font-size: 15px;
      font-weight: 600;
      color: grey;
      border-bottom: 1px solid grey;
      margin-bottom: 10px;
    }
  }

  .item__log--empty {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: gray;

    .empty--icon {
      font-size: 50px;
    }
    .empty--text {
      margin-top: 15px;
      font-size: 20px;
    }
  }
}

.item__edit--container {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  .add--area {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 5px;
    margin-bottom: 15px;

    input {
      flex: 1;
      height: 36px;
      outline: none;
      box-sizing: border-box;
      padding: 0px 10px;
    }

    button {
      height: 36px;
    }
  }

  ul {
    flex: 1;
    width: 100%;
    overflow-y: auto;

    &::-webkit-scrollbar {
      width: 2px;
      height: 2px;
    }
    &::-webkit-scrollbar-thumb {
      border-radius: 5px;
      background-color: #2c3333;
    }

    li {
      width: 100%;
      height: 40px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      box-sizing: border-box;
      overflow: hidden;
      padding: 0px 15px 0px 5px;
      font-size: 14px;

      .icon {
        cursor: pointer;
        transition: color 0.3s;

        &:hover {
          color: #dc3535;
        }
      }
    }
  }
}
